@import '@@/styles/consts.scss';

$maxHeight: 6vh + 0.6vh;

.dialog {
  width: 100%;
  height: $maxHeight;
  overflow: hidden;
  display: flex;
  margin-bottom: 1vh;
  transition: 0.5s ease-in-out;
  background-color: transparent;
  cursor: pointer;

  &__isChoosen {
    background-color: $borderGrey;
    cursor: auto;
    &:hover {
      background-color: $borderGrey !important; 
    }
  }
  &:hover {
    transition: 0.3s ease-in-out;
    background-color: $grey;
  }
  &__leftPart {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    &_imgPlug {
      background-color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 2.7vh !important;
      border: 1px solid $borderGrey;
      border-radius: 50%;
      width: 6vh !important;
      height: 6vh !important;
      margin: 0.6vh ;
    }
    

    &_online {
      position: absolute;
      bottom: 8%;
      right: 8%;
      height: 1.2vh;
      width: 1.2vh;
      border-radius: 50%;
      background-color: $blue;
    }
  }
  &__rightPart {
    margin-top: 5px;
    width: 100%;
    display: flex;
    flex-direction: column;
    &_message {
      width: 100%;
      display: flex;
      color: rgb(167, 167, 167);
      align-items: flex-start;

      &-infoCount {
        width: 1.5vw;
        height: 1.5vw;
        margin-right: 0.5vw;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid $blue;
        border-radius: 50%;
      }

      &-unreaded {
        width: 0.7vw;
        height: 0.7vw;
        content: '';
        margin-right: 0.5vw;
        margin-top: 0.5vw;
        border: 3px solid $blue;
        border-radius: 50%;
      }

      &-payload {
        width: 100%;
        height: 3vh;
        overflow: hidden;
      }
    }
  }
}