@import '@@/styles/consts.scss';

.dialogsSearch {
  width: 100%;
  height: 100%;
  border: 1px solid $borderGrey;
  
  display: flex;
  justify-content: center;
  align-items: center;

  input {
    width: 100%;
    height: 100%;
    border: none;
    &:hover {
      border: none;
      box-shadow: none;
    }
    &:focus {
      border: none;
      box-shadow: none;
    }
  }

  &__stopSearch {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2vw;

    &_icon {
      cursor: pointer;
      color: gray;
      &:hover {
        color: black;
      }
    }
  }
}