.login {
  display: flex;
  height: fit-content;
  width: fit-content;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  &__form {
    width: 20vw;
    form {
      height: 100%;
      display: flex;
      flex-direction: column;
      .formElement {
        flex: 1;
      }
    }

    &_submit {
      height: 100%;
      margin-top: 3px;
    }
  }
}