@import '@@/styles/consts.scss';

.chat {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-right: 1px solid $borderGrey;
  border-bottom: 1px solid $borderGrey;

  &__inputArea {
    display: flex;
    width: 100%;
    &_textArea {
      flex:12;
    }
    &_button {
      flex: 3;
    }
  }

  &__main {
    width: 100%;
    height: 81.2vh;
    overflow-x: hidden;
    overflow-y: auto;
    display: flex;
    align-items: flex-end;

    &_wrapper {
      width: 100%;
      height: fit-content;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      &-help {
        justify-content: center;
        align-items: center;
        height: 100%;
      }
    }
  }
}