@import '@@/styles/consts.scss';

.target {
  width: 10vh;
  height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: transparent;
  transition: 0.5s ease-in-out;
  &:hover {
    background-color: $grey;
    transition: 0.2s ease-in-out;
  }

  &_text {
    display: flex;
    justify-content: center;
    align-items: center;
  }

}