@import '@@/styles/consts';

@mixin tabsPositions ($flex) {
  display: flex;
  flex-direction: column;
  flex: $flex;
  & > section:nth-child(1) {
    flex: 0.6
  }
  & > section:nth-child(2) {
    flex: 12
  }

  margin-bottom: 5vh;
}

.messangerPage {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  &_main {
    width: 50vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    &__header {
      flex: 0.7;
    }
    &__chat {
      flex: 12;
      display: flex;
      &-left {
        @include tabsPositions(7)
      }
      &-right {
        @include tabsPositions(12)
      }
    }
  }
}

