@import url(https://cdnjs.cloudflare.com/ajax/libs/antd/4.4.2/antd.compact.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
.bread{width:100%;height:4vh;display:flex}.bread_links{margin-right:1vw}

.target{width:10vh;height:10vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:transparent;transition:0.5s ease-in-out}.target:hover{background-color:#fafafa;transition:0.2s ease-in-out}.target_text{display:flex;justify-content:center;align-items:center}


.message{width:100%;display:flex;margin-bottom:1.5vh;align-items:flex-end}.message-isMe{margin-left:1vh;justify-content:flex-start}.message-isPartner{margin-right:1vh;justify-content:flex-end}.message__cloud{width:65%;line-height:2.5vh;border-radius:1vh;display:flex;align-items:flex-end}.message__cloud-isMe{background-color:#1890ff;color:white !important}.message__cloud-isPartner{border:2px solid #1890ff;margin-right:1vh}.message__cloud_payload{width:100%;margin:0.3vw}.message__cloud_time{font-size:1.5vh !important;margin-right:0.5vh}.message__cloud_time-isMe{color:white !important}.message__cloud_time-isPartner{color:#1890ff !important}.message__unreaded{width:1.5vh;height:1.5vh;border:3px solid #1890ff;border-radius:50%;margin-left:1vh}

.dateElement{width:100%;display:flex;justify-content:center;align-items:center;margin-bottom:1vh}.dateElement__target{color:#B5B5B5}

.cPHeader{display:flex;background-color:#fafafa}.cPHeader__main{flex:7 1;display:flex;align-items:baseline;justify-content:center}.cPHeader_leftSpace{flex:3 1}.cPHeader_rightSpace{flex:3 1}

.chooseBoard{width:100%;height:100%;display:flex;flex-wrap:wrap;align-items:flex-start;align-content:flex-start;justify-content:space-between}

.login{display:flex;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;flex-direction:column;justify-content:center;align-items:center}.login__form{width:20vw}.login__form form{height:100%;display:flex;flex-direction:column}.login__form form .formElement{flex:1 1}.login__form_submit{height:100%;margin-top:3px}

.dialog{width:100%;height:6.6vh;overflow:hidden;display:flex;margin-bottom:1vh;transition:0.5s ease-in-out;background-color:transparent;cursor:pointer}.dialog__isChoosen{background-color:#f0f0f0;cursor:auto}.dialog__isChoosen:hover{background-color:#f0f0f0 !important}.dialog:hover{transition:0.3s ease-in-out;background-color:#fafafa}.dialog__leftPart{position:relative;display:flex;justify-content:center;align-items:center}.dialog__leftPart_imgPlug{background-color:white;display:flex;justify-content:center;align-items:center;font-size:2.7vh !important;border:1px solid #f0f0f0;border-radius:50%;width:6vh !important;height:6vh !important;margin:0.6vh}.dialog__leftPart_online{position:absolute;bottom:8%;right:8%;height:1.2vh;width:1.2vh;border-radius:50%;background-color:#1890ff}.dialog__rightPart{margin-top:5px;width:100%;display:flex;flex-direction:column}.dialog__rightPart_message{width:100%;display:flex;color:#a7a7a7;align-items:flex-start}.dialog__rightPart_message-infoCount{width:1.5vw;height:1.5vw;margin-right:0.5vw;display:flex;justify-content:center;align-items:center;border:2px solid #1890ff;border-radius:50%}.dialog__rightPart_message-unreaded{width:0.7vw;height:0.7vw;content:'';margin-right:0.5vw;margin-top:0.5vw;border:3px solid #1890ff;border-radius:50%}.dialog__rightPart_message-payload{width:100%;height:3vh;overflow:hidden}

.textReducer{width:100%;height:100%;display:flex;justify-content:flex-start;overflow:hidden}.textReducer__target{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

.cPMain{width:100%;height:100%;display:flex}.cPMain_main{flex:7 1;display:flex;align-items:center;justify-content:flex-start;flex-direction:column}.cPMain_leftSpace{flex:3 1}.cPMain_rightSpace{flex:3 1}

.choosePage{display:flex;height:100vh;width:100vw;flex-direction:column}.choosePage_leftSpace{flex:3 1}.choosePage_rightSpace{flex:3 1}

.testPage{display:flex;flex-direction:column;margin:20px 0 0 20px}.testPage_msg{margin-bottom:20px}

.loginPage{display:flex;height:100vh;width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center}

.chat{width:100%;height:100%;display:flex;flex-direction:column;justify-content:flex-end;align-items:center;border-right:1px solid #f0f0f0;border-bottom:1px solid #f0f0f0}.chat__inputArea{display:flex;width:100%}.chat__inputArea_textArea{flex:12 1}.chat__inputArea_button{flex:3 1}.chat__main{width:100%;height:81.2vh;overflow-x:hidden;overflow-y:auto;display:flex;align-items:flex-end}.chat__main_wrapper{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;justify-content:flex-end}.chat__main_wrapper-help{justify-content:center;align-items:center;height:100%}

.dialogsTab{width:100%;max-height:85.1vh;border-left:1px solid #f0f0f0;border-right:1px solid #f0f0f0;border-bottom:1px solid #f0f0f0;overflow-x:hidden;overflow-y:auto;display:flex}.dialogsTab__wrapper{width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column}.dialogsTab__wrapper-help{justify-content:center;align-items:center;height:100%}

.navigation{width:100%;height:100%}

.chatHeader{width:100%;height:100%;border-top:1px solid #f0f0f0;border-right:1px solid #f0f0f0;border-bottom:1px solid #f0f0f0;display:flex;justify-content:flex-start;align-items:center}.chatHeader__leftPart{display:flex;align-items:baseline;margin-left:1vw}.chatHeader__leftPart_partnerName{font-size:2.3vh !important}.chatHeader__leftPart_lastSeenDate{margin-left:1vw;color:#B5B5B5}

.dialogsSearch{width:100%;height:100%;border:1px solid #f0f0f0;display:flex;justify-content:center;align-items:center}.dialogsSearch input{width:100%;height:100%;border:none}.dialogsSearch input:hover{border:none;box-shadow:none}.dialogsSearch input:focus{border:none;box-shadow:none}.dialogsSearch__stopSearch{display:flex;justify-content:center;align-items:center;width:2vw}.dialogsSearch__stopSearch_icon{cursor:pointer;color:gray}.dialogsSearch__stopSearch_icon:hover{color:black}

.messangerPage{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.messangerPage_main{width:50vw;height:100%;display:flex;flex-direction:column}.messangerPage_main__header{flex:0.7 1}.messangerPage_main__chat{flex:12 1;display:flex}.messangerPage_main__chat-left{display:flex;flex-direction:column;flex:7 1;margin-bottom:5vh}.messangerPage_main__chat-left>section:nth-child(1){flex:0.6 1}.messangerPage_main__chat-left>section:nth-child(2){flex:12 1}.messangerPage_main__chat-right{display:flex;flex-direction:column;flex:12 1;margin-bottom:5vh}.messangerPage_main__chat-right>section:nth-child(1){flex:0.6 1}.messangerPage_main__chat-right>section:nth-child(2){flex:12 1}

body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#fff;overflow:hidden}div,p,h1,h2,h3,section,input,textarea,ul,li,button{margin:0;padding:0;font-family:'Roboto', sans-serif !important;font-size:1.8vh !important;font-style:normal;text-shadow:none;box-sizing:border-box}*{outline:none}button{height:100% !important;width:100%}.ant-btn-lg{line-height:1vh !important}.clickable{cursor:pointer}.pageWrapper{width:100vw;height:100vh}

