@import '@@/styles/consts.scss';

.dialogsTab {
  width: 100%;
  max-height: 85.1vh;
  border-left: 1px solid $borderGrey;
  border-right: 1px solid $borderGrey;
  border-bottom: 1px solid $borderGrey;

  overflow-x: hidden;
  overflow-y: auto;
  display: flex;

  &__wrapper {
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;

    &-help {
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }
}