@import '@@/styles/consts.scss';

.message {
  width: 100%;
  display: flex;
  margin-bottom: 1.5vh;
  align-items: flex-end;

  &-isMe {
    margin-left: 1vh;
    justify-content: flex-start;
  }

  &-isPartner {
    margin-right: 1vh;
    justify-content: flex-end;
  }


  &__cloud {
    width: 65%;
    line-height: 2.5vh;
    
    border-radius: 1vh;
    display: flex;
    align-items: flex-end;
    

    &-isMe {
      background-color: $blue;
      color: white !important;

    }

    &-isPartner {
      border: 2px solid $blue;
      margin-right: 1vh;
    }

    &_payload {
      width: 100%;
      margin: 0.3vw;
    }

    &_time {
      font-size: 1.5vh !important;
      margin-right: 0.5vh;
      

      &-isMe {
        color: white !important;
      }
    
      &-isPartner {
        color: $blue !important;
      }
    }
  }

  &__unreaded {
    width: 1.5vh;
    height: 1.5vh;
    border: 3px solid $blue;

    border-radius: 50%;
    margin-left: 1vh;
  }

}